@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";

.progress-bar {
  width: 100%;
  height: 10px;
  background-color: #f0f0f0;
  border-radius: 5px;
  margin-bottom: 1rem;
}

input[type="range"][orient="vertical"] {
  /* writing-mode: bt-lr; */
  /* -webkit-appearance: slider-vertical; */
  transform: rotateZ(270deg);
}
